// <reference no-default-lib="true"/>
/// <reference lib="es2015" />
/// <reference lib="webworker" />
/// <reference path="serviceworker.d.ts" />
var staticRessources = [
    { local: true, url: 'bundle.js' },
    { local: true, url: '/' },
    { local: true, url: '/index.html' },
    { local: true, url: 'assets/default_image.jpg' },
    { local: false, url: 'https://unpkg.com/react@16.13.1/umd/react.development.js' },
    { local: false, url: 'https://unpkg.com/react-dom@16.13.1/umd/react-dom.development.js' }
];
self.addEventListener('install', function (event) {
    event.waitUntil(caches.open('app-cache').then(function (cache) {
        return cache.addAll(staticRessources.map(function (e) { return e.url; })).then(function () { });
    }));
});
self.addEventListener('fetch', function (event) {
    var fontRegexp = /https:\/\/(fonts\.googleapis)|(fonts\.gstatic\.com)/;
    var flickrRegexp = /https:\/\/farm([0-9]+).staticflickr.com\/([0-9]+)\/([0-9]+)_([a-zA-Z0-9]+)_z.jpg/;
    if (event.request.url.match(fontRegexp)) {
        event.respondWith(fetch(event.request).then(function (response) {
            return caches.open('app-cache').then(function (cache) {
                cache.put(event.request, response.clone());
                return response;
            });
        }).catch(function () { return caches.match(event.request); }));
    }
    else {
        event.respondWith(fetch(event.request).catch(function () {
            if (event.request.url.match(flickrRegexp)) {
                return caches.match(event.request).then(function (match) {
                    if (match === undefined) {
                        if (event.request.destination === "image")
                            return caches.match('assets/default_image.jpg');
                        else
                            return new Response("Synthetic Reponse", { "status": 408, "headers": { "Content-Type": "text/plain" } });
                    }
                    else
                        return match;
                }).catch(function () {
                    return caches.match('assets/default_image.jpg');
                });
            }
            return caches.match(event.request);
        }));
    }
});
